import React from 'react';

class Main extends React.Component {

    render = () => {
        const list = this.props.listOfToDos.map(it => (
            <li key={it.id}
                style={it.done ? { textDecoration: "line-through" } : {}}
                onClick={() => this.props.toggler(it.id)}
            >
                {it.text}
            </li>
        ))
        return (
        < ul >
            {list}
        </ul >
        )
    }
}

export default Main;
