import './App.css';
import New from './components/New.js';
import Main from './components/Main';
import { BrowserRouter as Router, NavLink, Route, Switch } from 'react-router-dom';
import { Component } from 'react';


class App extends Component {

  state = {
    listOfToDos: [
        { id: 1, text: "Hakuna matata", done: false },
        { id: 2, text: "Summer is coming", done: true },
        { id: 3, text: "Za 2 tygodnie otwieramy...", done: false },
        { id: 4, text: "Chodź szogunie", done: false },
        { id: 5, text: "Grupa sroda parzysta", done: false },
      ]
  }

  adder = (text) => {

    const item = {
      id: this.state.listOfToDos.length+1,
      text: text,
      done: false
    }
    this.setState({
      listOfToDos: this.state.listOfToDos.concat(item)
    });

  }

  toggleItem = (id) => {
    const updatedList = this.state.listOfToDos.map( it => {
      if(it.id === id)
        it.done = ! it.done
      return it
    })

    this.setState({
      listOfToDos: updatedList
    })

  }
  render = () => (
    <Router>
      <header>Tu jest nagłówek</header>
      <main>

        <nav>
          <NavLink to="/" exact>Lista wszytkich</NavLink>
          <NavLink to="/new">Dodaj nowy</NavLink>
          <NavLink to="/trash">Śmietnik</NavLink>
        </nav>
        <Switch>
          <Route path="/" exact>
            <section><Main
            listOfToDos={this.state.listOfToDos}
            toggler={this.toggleItem}
            /></section>
          </Route>

          <Route path="/new">
            <section><New adder={this.adder}/></section>
          </Route>

          <Route path="/trash">
            <section><h2>To jest Śmietnik</h2></section>
          </Route>

          <Route><section>404 not found</section></Route>
        </Switch>
      </main>
      <footer>Stopka</footer>
    </Router>
  );
}

export default App;
